import React, { Fragment, useEffect, useState } from "react";
import "./Documentation.css";
import AOS from "aos";
import Base from "../../images/base.bmp";
import Sym from "../../images/sym.bmp";
import Rotate from "../../images/rotate.bmp";
import Filtre from "../../images/filtre.bmp";
import BlackAndWhite from "../../images/black_white.bmp";

export const Documentation = () => {
  useEffect(() => {
    AOS.init({
      duration: 1000,
      once: true,
    });
  }, []);
  const [saturation, setSaturation] = useState(100);
  const [borderWidth, setBorderWidth] = useState(5);
  const [greyscal, setGreyscal] = useState(0);
  const [contrast, setContrast] = useState(50);
  const [invert, setInvert] = useState(0);
  const [blur, setBlur] = useState(0);

  useEffect(() => {
    console.log(invert);
  }, [invert]);

  return (
    <Fragment>
      <div className="liste-container">
        <h2 className="card_title big">Documentation</h2>
      </div>
      <div className="liste-container">
        <center>
          <p>
            Voici l'image d'exemple que nous utiliserons pour illustrer l'effet
            de nos fonctionnalités Shotopof.
          </p>

          <img
            src={Base}
            alt="Illustration de dessert"
            className="base_image"
          />
        </center>
        <hr className="blue" />
        <div className="flex">
          <div>
            <h4 className="function">Bordure</h4>
            <p>Prototype : border(SDL_Surface *img)</p>
            <p>
              Cette fonction permet d'appliquer un contour à l'image. Nous
              proposerons des variantes de contour comme les pointillés.
            </p>
            <input
              style={{ width: "70%" }}
              type="range"
              min={0}
              max={80}
              value={borderWidth}
              onChange={(e) => setBorderWidth(e.target.value)}
            />
          </div>
          <div data-aos="fade-left">
            <img
              src={Base}
              style={{
                borderInlineStartWidth: `${borderWidth}px`,
                borderInlineEndWidth: `${borderWidth}px`,
                borderTopWidth: `${borderWidth}px`,
                borderBottomWidth: `${borderWidth}px`,
              }}
              alt="bordure"
              className="flex_image imgborder"
            />
          </div>
        </div>
        <hr className="blue" />
        <div className="flex">
          <div data-aos="fade-right">
            <img src={Sym} alt="Symétrie" className="flex_image" />
          </div>
          <div>
            <h4 className="function">Symétrie</h4>
            <p>Prototype : symh(SDL_Surface *img) (Symétrie horizontale)</p>
            <p>Prototype : symv(SDL_Surface *img) (Symétrie verticale)</p>
            <p>
              Cette fonction permet d'effectuer une symétrie de l'image par
              rapport à son axe horizontal ou vertical.
            </p>
          </div>
        </div>
        <hr className="blue" />
        <div className="flex">
          <div>
            <h4 className="function">Rotation</h4>
            <p>Prototype : rotate(SDL_Surface *img)</p>
            <p>
              Cette fonction permet d'effectuer une rotation de l'image de vers
              la droite ou la gauche. Les dimensions de l'images seront donc
              inversées.
            </p>
          </div>
          <div data-aos="fade-left">
            <img src={Rotate} alt="Rotation" className="rot" />
          </div>
        </div>
        <hr className="blue" />
        <div className="flex">
          <div data-aos="fade-right">
            <img
              src={Base}
              style={{ filter: `saturate(${saturation}%)` }}
              alt="Saturation"
              className="flex_image"
            />
          </div>
          <div>
            <h4 className="function">Saturation</h4>
            <p>Prototype : saturate(SDL_Surface *img)</p>
            <p>
              Cette fonction permet de saturer l'image et donc d'intensifier les
              couleurs présentes sur l'image.
            </p>
            <input
              style={{ width: "70%" }}
              type="range"
              min="100"
              max="800"
              value={saturation}
              onChange={(e) => setSaturation(e.target.value)}
            />
          </div>
        </div>
        <hr className="blue" />
        <div className="flex">
          <div>
            <h4 className="function">Nuance de gris</h4>
            <p>Prototype : greyscale(SDL_Surface *img)</p>
            <p>
              Cette fonction permet de transformer les couleurs en gris plus ou
              moins foncé selon la couleur d'origine.
            </p>
            <input
              style={{ width: "70%" }}
              type="range"
              min="0"
              max="100"
              value={greyscal}
              onChange={(e) => setGreyscal(e.target.value)}
            />
          </div>
          <div data-aos="fade-left">
            <img
              src={Base}
              style={{ filter: `grayscale(${greyscal}%)` }}
              alt="greyscale"
              className="flex_image"
            />
          </div>
        </div>
        <hr className="blue" />
        <div className="flex">
          <div data-aos="fade-right">
            <img
              src={Base}
              style={{ filter: `blur(${blur}px)` }}
              alt="Flou"
              className="flex_image"
            />
          </div>
          <div>
            <h4 className="function">Blur</h4>
            <p>Prototype : blur(SDL_Surface *img)</p>
            <p>
              Cette fonction permet de flouter l'image et ainsir rendre les
              contours moins discernables
            </p>
            <input
              style={{ width: "70%" }}
              type="range"
              min="0"
              max="6"
              value={blur}
              onChange={(e) => setBlur(e.target.value)}
            />
          </div>
        </div>
        <hr className="blue" />
        <div className="flex">
          <div>
            <h4 className="function">Filtre de couleur</h4>
            <p>Prototype : filter(SDL_Surface *img)</p>
            <p>
              Cette fonction permet de ne préserver que la couleur choisie
              (rouge, vert ou bleu). Ici la couleur choisie a été le rouge.
            </p>
          </div>
          <div data-aos="fade-left">
            <img src={Filtre} alt="Rotation" className="flex_image" />
          </div>
        </div>
        <hr className="blue" />
        <div className="flex">
          <div data-aos="fade-right">
            <img src={BlackAndWhite} alt="Flou" className="flex_image" />
          </div>
          <div>
            <h4 className="function">Mise en noir et blanc</h4>
            <p>Prototype : monochromatic(SDL_Surface *img)</p>
            <p>
              Cette fonction permet de modifier chaque pixel de l'image de sorte
              à ce que son code RGB soit (0,0,0) ou (255,255,255) en fonction de
              son code RGB d'origine.
            </p>
          </div>
        </div>
        <hr className="blue" />
        <div className="flex">
          <div>
            <h4 className="function">Contraste</h4>
            <p>
              Prototype : contrast(gdImagePtr img, FILE *fdout, char *path, int
              val)
            </p>
            <p>
              Cette fonction permet d'accentuer l'intensité de chaque couleur de
              l'image
            </p>
            <input
              style={{ width: "70%" }}
              type="range"
              min="50"
              max="300"
              value={contrast}
              onChange={(e) => setContrast(e.target.value)}
            />
          </div>
          <div data-aos="fade-left">
            <img
              src={Base}
              style={{ filter: `contrast(${contrast}%)` }}
              alt="Rotation"
              className="flex_image"
            />
          </div>
        </div>
        <hr className="blue" />
        <div className="flex">
          <div data-aos="fade-right">
            <img
              src={Base}
              style={{ filter: `invert(${invert})` }}
              alt="Negatif"
              className="flex_image"
            />
          </div>
          <div>
            <h4 className="function">Négative</h4>
            <p>Prototype : Negate(gdImagePtr img, FILE *fdout, char *path)</p>
            <p>
              Cette fonction permet d'inverser toutes les couleurs de chaque
              pixel de l'image
            </p>
            <center>
              <button
                className="buttonFunction"
                onClick={() => {
                  invert === 1 ? setInvert(0) : setInvert(1);
                }}
              >
                invert
              </button>
            </center>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
